module FSharpLangRu.App

open Fable.Core
open Fable.Core.JsInterop
open Fable.Helpers.React
open Components
open Elmish.React
open Elmish
             
let view model dispatch = 
    section [] [
        lightLayout "  F#?" "   ." [
            code [ ("simple-code", "   ", "code here")
                   ("convenient-syntax", " ", "syntax here")
                   ("powerful-types", "  ", "types here")
                   ("inline-functions", "  ", "inlines here3") ] ]
        darkLayout " F#?" "      ." [
            row [ card "" "F#   Linux, OS X, iOS, Windows, Android,    ." "desktop"
                  card "" "F#       ." "code-fork" 
                  card " " "F#    ,    ." "gears" 
                  card "Write less  do more" " F#       !" "code" ] ]
        lightLayout " !" "   F#  ." []
        darkLayout " ." ",     F#!" []
    ]

let init _ = 0
 
let update message count = 
    count + 1

Program.mkSimple init update view
|> Program.withReact "elmish-app"
|> Program.withConsoleTrace
|> Program.run 



